export class ElsaNavbar {
    constructor() {
        $(window).on('scroll', function() {
            if($(window).scrollTop() > 0) {
                $('#elsa-navbar').addClass('elsa-navbar');
            }else {
                $('#elsa-navbar').removeClass('elsa-navbar');
            }
        });

        $('#elsa-navbar-toggler').on('click', function() {
            $('#elsa-wrapper').toggleClass('elsa-offset');
            $(this).toggleClass('fa-navicon fa-close');
            if($('#elsa-wrapper').hasClass('elsa-offset')) {
                console.log('yes');
                $("html,body").scrollTop(0);
            }
        });

        // 下拉菜单
        $('.dropdown, .dropdown .dropdown-toggle').mouseover(function () { 
            $(this).addClass('show');
            $(this).children('.dropdown-menu').addClass('show');
        });
        $('.dropdown').mouseout(function () { 
            $(this).removeClass('show');
            $(this).children('.dropdown-menu').removeClass('show');
        });
    }
}