import { ElsaNavbar } from './ElsaNavbar'

class Elsa {
    constructor() {
        new ElsaNavbar()
        this.topTop()
    }

    topTop() {
        
        $("#elsa-to-top").on("click", function()
        {
            var time = Math.max( $("html, body").scrollTop() / ($(document).height() - $(window).height()), 0.4) * 1000;
            $("html, body").animate({
                scrollTop: 0
            }, time);
        });
    }
}

$(function()
{
    new Elsa()
});